.home-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 610px;
  width: 100%;
  background-color: rgb(255, 255, 255);
}
.container-box {
  width: 650px;
}
.upper-part {
  display: flex;
  justify-content: space-between;
}
.text-area {
  height: 300px;
  width: 330px;
}
.social-link ul {
  display: flex;
  list-style: none;
  padding: 0;
}
.social-link ul li {
  margin: 6px;
}
.img-box {
  height: 220px;
  border: 2px solid rgba(172, 171, 171, 0.517);
  border-radius: 50% 0% 80% 450% / 50% 0% 0% 230%;
  filter: drop-shadow(1px 2px 4px rgba(0, 0, 0, 0.629));
}

.bottom-part {
  display: flex;
  height: 40px;
  width: 100%;
  align-items: center;
}
.tech-icon ul {
  display: flex;
  margin: 0 0 0 30px;
  padding-left: 25px;
  border-left: 3px solid black;
}
.tech-icon ul li {
  list-style: none;
  margin: 0px 16.9px;
  filter: drop-shadow(2px 4px 6px grey);
}

@media only screen and (max-width: 600px) {
  .home-content {
    height: 815px;
    width: 100%;
    background-color: rgb(255, 255, 255);
  }
  .container-box {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .upper-part {
    display: flex;
    flex-direction: column-reverse;
  }
  .img-box {
    margin: 0px 0px 30px 40px;
  }
  .text-area {
    height: 250px;
    width: 300px;
  }
  .bottom-part {
    justify-content: center;
    width: 300px;
    height: 100%;
  }
  .tech-title {
    text-align: center;
  }
  .tech-icon ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    height: 100px;
    margin: 0 0 0 10px;
    padding: 10px 0px;
  }
  .tech-icon ul li {
    margin: 0px 15px 20px;
  }
}
